


import { ToastrService } from 'ngx-toastr';
// import { LoginService } from './login.service';
import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { FormBuilder, Validators, FormControl, FormGroup, FormArray } from '@angular/forms';
// import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { LoginService } from './login.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: []
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  loading = false;
  submitted = false;
  constructor(private _formBuilder: FormBuilder,
    private _router: Router,
    private _loginService: LoginService,
    private toastService: ToastrService,
    // private spinnerService: Ng4LoadingSpinnerService 
    ) {
    
     }


  ngOnInit() {
    this.buildLoginForm();
    console.log("in oninit")
  }
  get f() { return this.loginForm.controls; }

  onSubmit() {
    console.log("LoginComponent -> onSubmit -> onSubmit")
    this.submitted = true;
    if (this.loginForm.invalid) {
          return;
    }
    // this.spinnerService.show();
    this._loginService.post(this.loginForm.value).subscribe((response: any) => {
      if (response.status== 200) {
       this.toastService.success(response.message);
       localStorage.setItem('_token', JSON.stringify(response.token.token))
        // localStorage.setItem('_id', JSON.stringify(response.data._id));
       this._router.navigate(['/dashboard']);
      }
      else{
        // this.spinnerService.hide();
        this.toastService.error(response.message);
      }
    }, error => {
      // this.spinnerService.hide();
      this.toastService.error(error.message);
    });
  }

  buildLoginForm() {
    console.log("LoginComponent -> buildLoginForm -> buildLoginForm")
    this.loginForm = this._formBuilder.group({
      email: ['', [Validators.required, Validators.pattern(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/)]],
      password: ['', [Validators.required]]

    });
  }

remember(){
   console.log(this.loginForm.value)
}
}

